export type Language = {

    code: string;
    direction: string;

};

export type Provider = {

    args: any;

};

export type Decimal = {

    minusSign: string;
    decimalSeparator: string;
    thousandSeparator: string;

};
export type LoadingMode = number;
export let LoadingMode: any = {};
LoadingMode.Direct = 0;
LoadingMode.Async = 1;
LoadingMode[LoadingMode.Direct] = "Direct";
LoadingMode[LoadingMode.Async] = "Async";

export type ServiceState = number;
export let ServiceState: any = {};
ServiceState.isReady = 0;
ServiceState.isLoading = 1;
ServiceState.isWaiting = 2;
ServiceState[ServiceState.isReady] = "isReady";
ServiceState[ServiceState.isLoading] = "isLoading";
ServiceState[ServiceState.isWaiting] = "isWaiting";


export interface PropertyDecorator {

    <T extends Function>(type: T): T;

    (target: Object, propertyKey?: string | symbol): void;

}

export interface Type<T> extends Function {

    new (...args: any[]): T;

}

export interface Token {

    localeStorage?: Type<any>;
    translationProvider?: Type<any>;
    translationHandler?: Type<any>;

}
